.emibot-container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 40px;
  background-color: black;
  color: white;
}

.section-title {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 8px;
  color: white;
}

.section-subtitle {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 30px;
  color: wheat;
}

.feature-section {
  margin-bottom: 60px;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
}

.feature-card {
  background: #111827;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 3px 8px rgba(0,0,0,0.1);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-card img {
  max-width: 100%;
  border-radius: 8px;
  margin-bottom: 15px;
}

.feature-card h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: white;
}

.feature-card p {
  font-size: 0.95rem;
  line-height: 1.5;
  color: whitesmoke;
}

.feature-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 5px 14px rgba(0,0,0,0.15);
}
